  --animations: 250ms cubic-bezier(.4, 0, .2, 1);
  --placeholder-txt-color: #505050;
  --fruit-gradient: radial-gradient( 50% 50% at 50% 50%, #2f583f 0%, rgba(105, 105, 105, 0) 100% );
  --error-color: #ff705d;
  --backdrop-col: rgba(94, 99, 99, .7);
